import React, { useEffect, useState } from 'react';

import { useHttpClient } from '../../shared/hooks/http-hook';
import UsersList from '../components/UsersList';
import ErrorModal from '../../shared/components/UIElements/ErrorModal';
import LoadingSpinner from '../../shared/components/UIElements/LoadingSpinner';

const User = () => {
  const { isLoading, error, sendRequest, clearError } = useHttpClient();
  const [loadedUsers, setLoadedUsers] = useState();

  // run code only when certain dependencies change
  // 2nd arg. array is dependenencies, if left empty, will only run once
  // NOTE: DO NOT include async as part of first arg for useEffect (this React hook is not set up to expect a promise)
  useEffect(() => {
    // is a GET request (fetch default method is GET)
    // no headers needed b/c not attaching anything to request
    const fetchUsers = async () => {
      try {
        const responseData = await sendRequest(
          process.env.REACT_APP_BACKEND_URL + '/users'
        );

        setLoadedUsers(responseData.users);
      } catch (err) {
        // handled with hook logic
      }
    };
    fetchUsers();
    // useEffect relies on dependency sendRequest function, which comes from outside of the effect
  }, [sendRequest]);

  return (
    <React.Fragment>
      <ErrorModal error={error} onClear={clearError} />
      {isLoading && (
        <div className="center">
          <LoadingSpinner />
        </div>
      )}
      {/* only show if not loading and have list of users */}
      {!isLoading && loadedUsers && <UsersList items={loadedUsers} />}
    </React.Fragment>
  );
};

export default User;
